declare const window: any;
import { Resolver } from 'one-core';
import { settings } from 'one-loader';

export const loader = (
    options: any = {},
    init: (options: any) => void,
) => {
    window.onload = async () => {
        const {
            log,
            url = 'settings.json',
        } = options;
        
        const resolver: Resolver = (url: string) => (
            url
        );

        const resolved = resolver(url);
        const resp = await settings(
            resolved, options,
        );

        init({
            resolver,
            settings: resp,
        });
    }
};