import { array } from 'one-core';
import { Address } from 'one-model';

export const address = (
    value: any = {},
    context: any = {},
): Address => {
    const {
        Street,
        Email,
        City,
        County,
        State,
        PostalCode,
        Country,
        PhoneNumbers = [],
    } = value;

    const result: Address = {
        street: Street,
        email: Email,
        city: City,
        county: County,
        country: Country,
        state: State,
        zip: PostalCode,
        phones: array(PhoneNumbers).map((p: any) => {
            const {
                Type,
                Number,
            } = p;
            return {
                num: Number,
                type: (
                    (Type) && (Type.toUpperCase())
                ),
            };
        }),
    };

    return result;
};
